.hero {
  margin: 0 auto;
  padding-top: 80px;

  background-color: #c4c4c4;

  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  text-align: center;
	max-width: 360px;
  height: 500px;

  background-image: linear-gradient(
      to right,
      rgba(47, 48, 58, 0.4),
      rgba(47, 48, 58, 0.4)
    ),
    url(../images/hero/hero-image-1-sm.jpg);

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        to right,
        rgba(47, 48, 58, 0.4),
        rgba(47, 48, 58, 0.4)
      ),
      url(../images/hero/hero-image-1-sm@2x.jpg);
  }

  @media screen and (min-width: 481px) {
    background-image: linear-gradient(
        to right,
        rgba(47, 48, 58, 0.4),
        rgba(47, 48, 58, 0.4)
      ),
      url(../images/hero/hero-image-1-md.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          rgba(47, 48, 58, 0.4),
          rgba(47, 48, 58, 0.4)
        ),
        url(../images/hero/hero-image-1-sm@2x.jpg);
    }
  }

	@media screen and (min-width: 768px) {
		max-width: 768px;
    // padding-bottom: 30px;
  }

  @media screen and (min-width: 1440px) {
    // padding-bottom: 50px;
  }


