}

.hero__container {
  max-width: 300px;
  margin-left: 0;
  color: var(--main-background-color);
  text-align: left;

	@media screen and (min-width: 768px) {
    max-width: 506px;
		padding-left: 100px;
  }


}

.hero__title {
  font-family: var(--fouth-font);
  font-weight: 700;
  margin-bottom: 30px;
  font-size: 44px;
  line-height: 1.17;
  letter-spacing: 0.05em;

  @media screen and (min-width: 480px) {
    font-size: 48px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 72px;
    line-height: 1.18;
  }
}

