.socials {
  display: flex;
}


.socials__item:not(:last-child) {
  margin-right: 10px;
}

.socials {
  margin-bottom: 20px;
}

.socials__link {
  transition: color 250ms var(--transition-timing);

  &:hover,
  &:focus {
    color: var(--accent-color);
  }
}

.socials__icon {
  --color3: currentColor;
}